import React from 'react';
import { Form, InputGroup, Container } from 'react-bootstrap';
import LoaderButton from "./LoaderButton.js";
import CountDownButton from "./CountDownButton.js";

function signin(props) {
  return(
    <Container style={{"maxWidth": 320}}>
      <Form onSubmit={props.onSubmit} >
        <Form.Group controlId="email">
          <Form.Label>Email</Form.Label>
          <Form.Control 
          autoFocus
          type="email"
          value={props.email}
          onChange={props.onChange}/>
        </Form.Group>
        <Form.Group controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control 
            value={props.password}
            onChange={props.onChange}
            type="password"/>
        </Form.Group>
        <div className="text-center">
          <LoaderButton 
          variant="outline-info" 
          type="submit"
          text="Login"
          loadingText="Logging in…"
          isLoading={props.isLoading}
          />
        </div>
      </Form>
    </Container>
  );
}

function signup(props) {
  return(
    <Container style={{"maxWidth": 320}}>
      {
        props.newUser === null ?
        <Form onSubmit={props.onSignupSubmit}>
          <Form.Group controlId="email">
            <Form.Label>Email</Form.Label>
            <Form.Control 
            autoFocus
            type="email"
            value={props.email}
            onChange={props.onChange}/>
          </Form.Group>
          <Form.Group controlId="password">
            <Form.Label>Password</Form.Label>
            <Form.Control 
              value={props.password}
              onChange={props.onChange}
              type="password"/>
          </Form.Group>
          <Form.Group controlId="confirmPassword">
            <Form.Label>Confirm Password</Form.Label>
            <Form.Control 
              value={props.confirmPassword}
              onChange={props.onChange}
              type="password"/>
          </Form.Group>
          <div className="text-center">
            <LoaderButton 
            variant="outline-info" 
            type="submit"
            text="Signup"
            loadingText="Signing up…"
            isLoading={props.isLoading}
            />
          </div>
        </Form>
        :
        <Form onSubmit={props.onConfirmationSubmit}>
          <Form.Label>{props.email}</Form.Label>
          <Form.Group controlId="confirmationCode">
            <Form.Label>Confirmation Code</Form.Label>
             <InputGroup className="mb-3">
              <Form.Control
                autoFocus
                type="password"
                value={props.confirmationCode}
                onChange={props.onChange}
              />
              <InputGroup.Append>
                <CountDownButton
                  variant="outline-secondary" 
                  text="Send Code"
                  onClick={props.onResend}
                  loadingText="Sending…"
                  isSending={props.isSending}
                  seconds={props.seconds}
                />
                
              </InputGroup.Append>
            </InputGroup>
            <Form.Text className="text-muted">
              Please check your email for the code.
            </Form.Text>
          </Form.Group>
          <div className="text-center">
            <LoaderButton 
            variant="outline-info" 
            type="submit"
            text="Verify"
            loadingText="Verifying…"
            isLoading={props.isLoading}
            />
          </div>
        </Form>
      }
    </Container>
  );
}

function resetPassword(props) {
  return(
    <Container style={{"maxWidth": 320}}>
      <Form onSubmit={props.onResetPassword}>
        <Form.Group controlId="email">
          <Form.Label>Email</Form.Label>
          <InputGroup className="mb-3">
          <Form.Control 
          autoFocus
          type="email"
          value={props.email}
          onChange={props.onChange}/>
          <InputGroup.Append>

            <CountDownButton
            variant="outline-secondary" 
            text="Send Code"
            onClick={props.onSendForgetCode}
            loadingText="Sending…"
            isSending={props.isSending}
            seconds={props.seconds}
            />

          </InputGroup.Append>
          </InputGroup>
        </Form.Group>
        <Form.Group controlId="password">
          <Form.Label>New Password</Form.Label>
          <Form.Control 
            value={props.password}
            onChange={props.onChange}
            type="password"/>
        </Form.Group>
        <Form.Group controlId="confirmationCode">
          <Form.Label>Confirmation Code</Form.Label>
           
            <Form.Control
              autoFocus
              type="password"
              value={props.confirmationCode}
              onChange={props.onChange}
            />
            
          
          <Form.Text className="text-muted">
            Please check your email for the code.
          </Form.Text>
        </Form.Group>
        <div className="text-center">
          <LoaderButton 
          variant="outline-info" 
          type="submit"
          text="Reset"
          loadingText="Resetting…"
          isLoading={props.isLoading}
          />
        </div>
      </Form>
    </Container>
  );
}

class LogModal extends React.Component {
  render() {

    return (
      <>
        {
          this.props.isSignup ? 
          signup(this.props)
          :
          (
            this.props.isForgetPassword ?
            resetPassword(this.props)
            :
            signin(this.props)  
          )
          
        }
      </>
    );
  }
}

export default LogModal