import React from "react";
import { Button, Spinner } from "react-bootstrap";
import "../css/LoaderButton.css";

export default ({
  variant,
  isLoading,
  text,
  loadingText,
  className = "",
  // disabled = false,
  ...props
}) =>
  <Button
    variant= {variant}
    className={`LoaderButton ${className}`}
    {...props}
  >
    {isLoading && <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />}
    {!isLoading ? text : loadingText}
  </Button>;

//&& <Glyphicon glyph="refresh" className="spinning" />