import React from 'react';
import { Auth } from "aws-amplify";
// import Navigation from './components/js/Navigation';
import Header from './components/js/Header';
import Main from './components/js/Main';

class App extends React.Component {
	constructor(props) {
	    super(props);

	    this.state = {
	    	alertType: "warning",
	      	alertShow: false,
	      	alertMessage: "",
	      	isForgetPassword: false,
			isSignup: false,
			signTitle: "Sign in",
			signStatement: "New to SayYourMovie? Join us!",
			show: false,
			username: "",
			email: "",
			password: "",
			confirmPassword: "",
			confirmationCode: "",
			newUser: null,
			isAuthenticated: false,
			isAuthenticating: true,
			isLoading: false,
			isSending: false,
			seconds: 30,
	    };

	    this.handleSendForgetCode = this.handleSendForgetCode.bind(this);
	    this.handleForget = this.handleForget.bind(this);
	    this.countDown=this.countDown.bind(this);
	    this.handleResetPassword = this.handleResetPassword.bind(this);

	    this.handleCloseAlert = this.handleCloseAlert.bind(this);

	    this.handleSign = this.handleSign.bind(this);

	    this.handleShow = this.handleShow.bind(this);
	    this.handleClose = this.handleClose.bind(this);
	    this.handleChange = this.handleChange.bind(this);

	    this.handleSubmit = this.handleSubmit.bind(this);

	    this.handleLogout = this.handleLogout.bind(this);

	    this.handleResend = this.handleResend.bind(this);

	    this.handleSignupSubmit = this.handleSignupSubmit.bind(this);
	    this.handleConfirmationSubmit = this.handleConfirmationSubmit.bind(this);
	}

	async componentDidMount() {
	  try {
	    await Auth.currentSession();
	    this.setState({ isAuthenticated: true });
	  }
	  catch(e) {
	    if (e !== 'No current user') {
	      alert(e);
	    }
	  }
	  this.setState({ isAuthenticating: false });
	}

	countDown(){
      if(this.state.seconds > 0) {
          this.setState({
            seconds: this.state.seconds - 1
          })
          setTimeout(() => this.countDown(), 1000);
      }else {
         this.setState({
          isSending: false,
          seconds: 30,
         })
      }   
  	}

	handleCloseAlert() {
		this.setState({
			alertShow: false,
		})
	}

	handleForget() {
		this.setState({
			isForgetPassword: true,
			signTitle: "Reset Password"
		})
	}

	handleSign() {
		if (!this.state.isSignup) {
			if (this.state.isForgetPassword) {
				this.setState({
					isForgetPassword: false,
					isSignup: false,
					signTitle: "Sign in",
					signStatement: "New to SayYourMovie? Join us!"
				})
				return;
			}
			this.setState({
				isSignup: true,
				signTitle: "Sign up",
				newUser: null,
				signStatement: "Already in? Sign in!"
			});
		}
		else {
			this.setState({
				isSignup: false,
				signTitle: "Sign in",
				isForgetPassword: false,
				signStatement: "New to SayYourMovie? Join us!"
			});
		}
	}

	validateSignupForm() {
		return (
			this.state.email.length > 0 &&
		    this.state.password.length > 0 &&
		    this.state.password === this.state.confirmPassword
		);
	}

	validateSigninForm() {
		return (
			this.state.email.length > 0 &&
		    this.state.password.length > 0
		);
	}

	validateConfirmationForm() {
	    return this.state.confirmationCode.length > 0;
	}

	handleClose() {
		this.setState({ show: false });
	}

	handleShow() {
		this.setState({ show: true });
	}

	handleChange(event) {
	    this.setState({
	      [event.target.id]: event.target.value
	    });
    }

    async handleResend() {
    	this.setState({ isSending: true });
	    this.countDown();
	    try {
		    Auth.resendSignUp(this.state.email);
		} catch (e) {
		    alert(e.message);
		}
    }

    async handleLogout() {
    	await Auth.signOut();
  		this.setState({ 
  			isAuthenticated: false,
  			isLoading: false,
  			isSignup: false,
  			newUser: null,
  			signTitle: "Sign in",
			signStatement: "New to SayYourMovie? Join us!"
  		});
    }

    async handleSignupSubmit(event) {
    	event.preventDefault();
	    this.setState({ isLoading: true });

	    if (!this.validateSignupForm()) {
	    	this.setState({ isLoading: false });
	    	this.setState({alertShow: true, alertMessage: "Incorrect input.", alertType:"warning"});
	    	return;
	    }

	    try {
		    const newUser = await Auth.signUp({
		      username: this.state.email,
		      password: this.state.password
		    });
		    
		    this.setState({
		      newUser
		    });

		    this.setState({ isSending: true });
	    	this.countDown();
		} catch (e) {
			if (e.name === 'UsernameExistsException') {
				this.setState({alertShow: true, alertMessage: "An account with the given email already exists.", alertType:"warning"})
			}
			else {
				alert("Something wrong...");	
			}
		    
		}
	    this.setState({ isLoading: false });
    }

    async handleConfirmationSubmit(event) {
	    event.preventDefault();
	    this.setState({ isLoading: true });

	    if (!this.validateConfirmationForm()) {
	    	this.setState({ isLoading: false });
	    	this.setState({alertShow: true, alertMessage: "Incorrect input.", alertType:"warning"});
	    	return;
	    }

	    try {
		    await Auth.confirmSignUp(this.state.email, this.state.confirmationCode);
		    await Auth.signIn(this.state.email, this.state.password);
		    this.setState({ 
		    	isAuthenticated: true,
		    	show: false
		    });
		} catch (e) {
			if (e.name === 'CodeMismatchException') {
				this.setState({
					alertShow: true, 
					alertMessage: "Invalid verification code provided, please try again.",
					alertType:"warning"
				})	
			}
			else {
				alert("Something wrong...");	
			}
		    
		    this.setState({ isLoading: false });
		}
	}

	async handleSendForgetCode(event) {
	    event.preventDefault();
	    this.setState({ isSending: true });
	    this.countDown();
	    if (this.state.email === "") {
	    	// this.setState({ isSending: false });
	    	this.setState({alertShow: true, alertMessage: "Email cannot be empty.", alertType:"warning"});
	    	return;
	    }

	    try {

	    	await Auth.forgotPassword(this.state.email)

		} catch (e) {
			if (e.name === 'CodeMismatchException') {
				this.setState({
					alertShow: true, 
					alertMessage: "Invalid verification code provided, please try again.",
					alertType:"warning"
				})	
			}
			else {
				alert("Something wrong...");	
				console.log(e.message);
			}
		}
	}

	async handleResetPassword(event) {
	    event.preventDefault();
	    this.setState({ isLoading: true });

	    // if (this.state.email === "") {
	    // 	this.setState({ isLoading: false });
	    // 	this.setState({alertShow: true, alertMessage: "Email cannot be empty."});
	    // 	return;
	    // }

	    try {
	    	await Auth.forgotPasswordSubmit(this.state.email, this.state.confirmationCode, this.state.password)

	    	this.setState({ 
		    	isForgetPassword: false,
		    	alertMessage: "Successfully reset password, you can login now.",
		    	alertType: "success",
		    	alertShow: true,
		    });
		} catch (e) {
			if (e.name === 'CodeMismatchException') {
				this.setState({
					alertShow: true, 
					alertMessage: "Invalid verification code provided, please try again.",
					alertType:"warning"
				})	
			}
			else {
				alert("Something wrong...");	
			}
		}
		this.setState({ isLoading: false});
		
	}

    async handleSubmit(event) {
	    event.preventDefault();
	    this.setState({ isLoading: true });

	    if (!this.validateSigninForm()) {
	    	this.setState({ isLoading: false });
	    	this.setState({alertShow: true, alertMessage: "Incorrect input.", alertType:"warning"});
	    	return;
	    }

	    try {
		    await Auth.signIn(this.state.email, this.state.password);
		    this.setState({ 
		    	isAuthenticated: true,
		    	show: false
		    });
		  } catch (e) {
			if (e.name === 'UserNotConfirmedException') {
				this.setState({
					alertShow: true, 
					alertMessage: "The email has not been confirmed yet.",
					alertType:"warning",
					isSignup: true,
					newUser: this.state.email,
					signTitle: "Please verify your account",
					signStatement: "Already in? Sign in!"
				})	
			}
		    else if (e.name === 'NotAuthorizedException'){
		    	this.setState({alertShow: true, alertMessage: "Incorrect email or password.", alertType:"warning"})
		    }
		    else {
		    	alert("Something wrong...");	
		    }
		    
		    this.setState({ isLoading: false });
		  }
	}

	render() {
		return(
			<div>
				<Header 
				alertType={this.state.alertType}
				alertMessage={this.state.alertMessage}
				alertShow={this.state.alertShow}
				isForgetPassword={this.state.isForgetPassword}
				signTitle={this.state.signTitle}
				isSignup={this.state.isSignup}
				signStatement={this.state.signStatement}
				handleSign={this.handleSign}
				handleForget={this.handleForget}
				isLoading={this.state.isLoading}
				isSending={this.state.isSending}
				seconds={this.state.seconds}
				show={this.state.show}
				onCloseAlert={this.handleCloseAlert}
				onShow={this.handleShow}
				onClose={this.handleClose}
				onSubmit={this.handleSubmit}
				onSignupSubmit={this.handleSignupSubmit}
				onConfirmationSubmit={this.handleConfirmationSubmit}
				onResend={this.handleResend}
				onSendForgetCode={this.handleSendForgetCode}
				onResetPassword={this.handleResetPassword}
				onChange={this.handleChange}
				onLogout={this.handleLogout}
				newUser={this.state.newUser}
				confirmPassword={this.state.confirmPassword}
				confirmationCode={this.state.confirmationCode}
				email={this.state.email}
				password={this.state.password}
				isAuthenticated={this.state.isAuthenticated}/>

				<Main 
				email={this.state.email}
				password={this.state.password}/>


			</div>
		);
	}
}

export default App