import React from 'react';
import { Link } from 'react-router-dom';
import "../css/LinkStyle.css"
// Bootstrap
import { Navbar, Container, Row, Col, Card, Carousel} from 'react-bootstrap';
import "../css/Carousel.css"
import {getTopMovies} from './Pages.js'
import styled from 'styled-components';


class CardSliderCol extends React.Component {
	render() {
		const MaxP = styled.figcaption`
			overflow: hidden;
			  display: -webkit-box;
			  -webkit-line-clamp: 1;
			  -webkit-box-orient: vertical;
		`;
		return (
			<Col>
	  			<figure className="figure">
                    <Link to={{ pathname: '/Movie-Website/movie-page/' + this.props.movie.Title, state: {movieInfo: this.props.movie} }}>
						<img onError={(e)=>{e.target.onerror = null; e.target.src="https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg"}} style={{width: 180, height: 200}}  src={this.props.movie.Poster}  alt="" />
					</Link>
					<Link to={{ pathname: '/Movie-Website/movie-page/' + this.props.movie.Title, state: {movieInfo: this.props.movie} }}>
                    	<MaxP className="figure-caption text-right">{this.props.movie.Title}</MaxP>
                    </Link>
                </figure>
	  		</Col>
		);
	}
}

class CardSliderRow extends React.Component {
	render() {
		return (
			<Row>
				{this.props.movies.map((item, i) => 
					<CardSliderCol key={item.Title} movie={item}/>	
				)}
		  	</Row>
		);
	}
}

class CardSlider extends React.Component {
	render() {
		return (
			<Carousel indicators={false} interval={999999}>
			  <Carousel.Item>
			  	<CardSliderRow movies={this.props.movies.slice(0, 5)} />
			  </Carousel.Item>
			  <Carousel.Item>
			  	<CardSliderRow movies={this.props.movies.slice(5, 10)}/>
			  </Carousel.Item>
			</Carousel>
		);
	}
}

class RecMovieCard extends React.Component {
	render() {
		return (
			<Card bg="dark" border="info">
			  <Card.Header className="shadow-lg" >
			  	<Navbar.Brand as={Link} to={"/Movie-Website/" + this.props.series}>
			  		<h2 className="text-white py-2 pr-4 m-0 display-4 font-weight-bold">
				  		{this.props.series + " Movies"}
              		</h2>
                </Navbar.Brand>
              </Card.Header>
			  <Card.Body>
			  	<CardSlider movies={this.props.movies}/>
			  </Card.Body>
			</Card>
		);
	}
}

class RecContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			movies: [],
		}
	}
	componentDidMount() {
		getTopMovies(10)
		.then(response => 
			{	
				console.log(JSON.parse(JSON.parse(response.movie)));
				this.setState({movies: JSON.parse(JSON.parse(response.movie))});
			}
		)
	}
	render() {	
		if (this.state.movies.length === 0) return null;		  	
		return (
			<Container>
			  <Row key={"Top Rated Movies"} className="shadow-lg" style={{marginTop:10}}>
			  	<RecMovieCard series="Top" movies={this.state.movies}/>
			  </Row>
	        </Container>
			
		);
	}	
}

