class CustomMenu extends React.Component {
  constructor(props, context) {
    super(props, context);

    this.handleChange = this.handleChange.bind(this);

    this.state = { value: '' };
  }

  handleChange(e) {
    this.setState({ value: e.target.value.toLowerCase().trim() });
  }

  render() {
    const {
      children,
      style,
      'aria-labelledby': labeledBy,
    } = this.props;

    const { value } = this.state;

    return (
	      <div style={style} aria-labelledby={labeledBy}>
	      	<Container>
	      		<Row>
			        <FormControl
			          className="mx-3 my-2 w-auto align-self-center"
			          placeholder="Type to filter..."
			          onChange={this.handleChange}
			          value={value}
			        />
	        	</Row>
	        
				<Row>
					<Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(0, 6).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				    <Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(6, 12).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				    <Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(12, 18).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				    <Col>
				        <ul className="list-unstyled">
				          {React.Children.toArray(children).slice(18, 24).filter(
				            child => !value || child.props.children.toLowerCase().startsWith(value),
				          )}
				        </ul>
				    </Col>
				</Row>

				

				
		    
		    </Container>
	      </div>
	    
    );
  }
}

class CustomItems extends React.Component {
	render() {
		return (
			<Dropdown.Menu as={CustomMenu}>
				{GENRE.map((type, i) => 
					<Dropdown.Item 
						key={type}
						eventKey={type}
						as={Link}
						onClick={() => this.props.onClick()}
						to={'/Movie-Website/' + type}
					>
					{type}
					</Dropdown.Item>
				)}
			</Dropdown.Menu>
		);
	}
}

class NavLink extends React.Component {
	render() {
		return (
			<Nav.Item>
                <Nav.Link
                active={this.props.isActive ? true: false}
                className="nav-link" 
                as={Link}
                disabled={this.props.disabled}
                to={this.props.path}
                onClick={() => this.props.onClick()}>
                {this.props.text}
                </Nav.Link>
        	</Nav.Item>
		);
	}
}




class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			links: [
				{path: "/Movie-Website/", text: "Home", isActive: this.props.location.pathname === "/Movie-Website/"? true : false},
				{path: "/Movie-Website/Recommend", text: "Recommend", disabled: true, isActive: this.props.location.pathname === "/Movie-Website/Recommend"? true : false},
				{path: "/Movie-Website/Top", text: "Top Rated", isActive: this.props.location.pathname === "/Movie-Website/Top"? true : false},
				{path: "/Movie-Website/Popular", text: "Popular", disabled: true, isActive: this.props.location.pathname === "/Movie-Website/Popular"? true : false},
				{path: "/Movie-Website/AdvancedSearch", text: "Search", isActive: this.props.location.pathname === "/Movie-Website/AdvancedSearch"? true : false},
			]
		}
		this.handleClick = this.handleClick.bind(this);
	}

	

	handleClick(i) {
		const links = this.state.links.slice();
		for (var j = 0; j < links.length; j++) {
			links[j].isActive = i === j;
		}
		this.setState({links: links});
	}


	render() {
		const LinkStyle = styled(Nav.Link)``;
		return(
			<div>
			    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" fixed="top">
				  <Navbar.Brand as={Link} to="/Movie-Website/">SayYourMovie</Navbar.Brand>
				  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
				  <Navbar.Collapse id="responsive-navbar-nav">
				    <Nav fill className="mr-auto" style={{width:"50%"}}>
				      {this.state.links.map((link, i) =>
			      		<NavLink inline
			      			key={link.text}
			      			path={link.path}
			      			text={link.text}
			      			isActive={link.isActive}
			      			disabled = {link.disabled}
			      			onClick={() => this.handleClick(i)}
			      		/>
				      )}
			          <Nav.Item>
			          	<NavDropdown title="Genre" className="dropdown full-width" id="dropdown-menu">
						    <CustomItems onClick={() => this.handleClick(-1)}/>
				      	</NavDropdown>
			          </Nav.Item>
			        </Nav>

			        <Nav className="mr-auto">
			        	<Form inline>
						    <Search />
						</Form>
			        </Nav>

				    <Nav>
				    	<Navbar.Text>
				    	<Nav.Item>
				    		<span className="badge badge-pill badge-light"> Hi </span> 
				    	</Nav.Item>
						</Navbar.Text>
						{
							this.props.isAuthenticated ?
							<Nav.Link onClick={this.props.onLogout} className="nav-link text-white">
								Logout
							</Nav.Link>
							:
							<Nav.Link onClick={this.props.onShow} className="nav-link text-white">
								Signin
							</Nav.Link>
							
						}
						
				    </Nav>
				  </Navbar.Collapse>
				</Navbar>

				<Modal  show={this.props.show} onHide={this.props.onClose}>
		          <Modal.Header closeButton>
		            <Modal.Title>{this.props.signTitle}</Modal.Title>
		          </Modal.Header>
		          <Modal.Body>
		          	<Alert className="text-center" variant={this.props.alertType} show={this.props.alertShow} onClose={this.props.onCloseAlert} dismissible>
			          {this.props.alertMessage}
			        </Alert>

		          	<LogModal  
		          	username={this.props.username}
		          	email={this.props.email}
		          	password={this.props.password}
		          	newUser={this.props.newUser}
					confirmPassword={this.props.confirmPassword}
					confirmationCode={this.props.confirmationCode}
					onResend={this.props.onResend}
		          	onChange={this.props.onChange}
		          	onSubmit={this.props.onSubmit}
		          	onSignupSubmit={this.props.onSignupSubmit}
					onConfirmationSubmit={this.props.onConfirmationSubmit}
					onSendForgetCode={this.props.onSendForgetCode}
					onResetPassword={this.props.onResetPassword}
		          	isAuthenticated={this.props.isAuthenticated}
		          	isSignup={this.props.isSignup}
		          	isForgetPassword={this.props.isForgetPassword}
		          	isLoading={this.props.isLoading}
		          	isSending={this.props.isSending}
		          	seconds={this.props.seconds} />

		          	<Container fluid={true}>
		          			{
		          				!this.props.isSignup ?
		          				(	
		          					this.props.isForgetPassword ?
		          					<Row>
										<Col md={5}>
											<LinkStyle style={{"color": "#006699"}} onClick={this.props.handleSign}>
												{"Back"}
											</LinkStyle>
										</Col>
									</Row>
		          					:
		          					<Row>
			          					<Col md={7}>
						          			<LinkStyle onClick={this.props.handleSign} style={{"color": "#006699"}}>
												{this.props.signStatement}
											</LinkStyle>
										</Col>
										<Col md={5}>
											<LinkStyle style={{"color": "#006699"}} onClick={this.props.handleForget}>
												{"Forget Password?"}
											</LinkStyle>
										</Col>
									</Row>
									
		          				)
		          				:
		          				(
		          					<Row>
			          					<Col md={{ offset: 7 }}>
						          			<LinkStyle onClick={this.props.handleSign} style={{"color": "#006699"}}>
												{this.props.signStatement}
											</LinkStyle>
										</Col>
									</Row>
		          				)
		          			}
					</Container>
		          </Modal.Body>
		        </Modal>
			</div>
		);
	}
}

export default withRouter(Header)