class TopRated extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			movies: [],
		}
	}
	componentDidMount() {
		getTopMovies(10)
		.then(response => this.setState({movies: JSON.parse(JSON.parse(response.movie))}))
	}
	render() {
		if (this.state.movies.length === 0) return null;
		return (
			<React.Fragment>
				<Container style={{marginTop: 150}}>
					{this.state.movies.map((item, index) => 
						<TopMovie key={"top-" + index} movie={item} order={index + 1}/>
					)}
				</Container>
			</React.Fragment>
		);
	}
}


class SearchPage extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			text: "",
			movies: [],
		}
		this.handleClick = this.handleClick.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}

	handleClick(event) {
		event.preventDefault();
		advancedSearch({"text": this.state.text})
		.then(res => this.setState({
			movies: JSON.parse(res.movies),
		})).then(()=>console.log(this.state.movies));
	}
	
	handleChange(event) {
	    this.setState({
	      text: event.target.value
	    });
    }

	render() {
		return (
			<React.Fragment>
				<Container>
					<Form className="text-center">
						<Form.Group controlId="exampleForm.ControlTextarea1">
						    <Form.Label>Example textarea</Form.Label>
						    <Form.Control as="textarea" rows="3" 
						    onChange={this.handleChange} 
						    value={this.state.text}/>
						</Form.Group>
						<Button 
				          variant="outline-info" 
				          type="submit"
				          onClick={this.handleClick}
				        >
				        Submit
				        </Button>
					</Form>
					<Row>
						<h2 style={{color:"white"}}>Results:</h2>
						<Container>
							{this.state.movies.length !== 0 ?
								this.state.movies.map((item, index) => 
								<TopMovie key={"top-" + index} movie={item} order={index + 1}/>)
								:
								<h3 style={{color:"white"}} className="text-center">No Results...</h3>
							}
						</Container>
					</Row>
				</Container>
			</React.Fragment>
		);
	}
}


